import React from 'react';
import {useSelector} from "react-redux";
import {RootState} from "../store/reducers";
import {BarChart} from "./BarChart";
import {selectTodayStatistics, selectTypeWeek} from "../store/selectors/statistics";
import {AdditionalInfoContainer} from "./AdditionalInfoContainer";
import {PomidorInfo} from "./PomidorInfo";
import {IDayliStatistics, TypeWeek} from "../store/types";
import {SelectWeek} from "./SelectWeek";
import {WorkInfoCard} from "./WorkInfoCard";
import {Text} from "../Text";

export function Statistics() {
    const typeWeek = useSelector<RootState, TypeWeek>(selectTypeWeek);
    const dayliStatistics = useSelector<RootState, IDayliStatistics | null>(selectTodayStatistics);

    return (
        <>
            <div style={{display:"flex", flexDirection:"column", padding:'88px 80px 99px 80px', gap:'30px'}}>
                <div style={{display:"flex", flexDirection:"row", justifyContent:'space-between', alignItems:'center'}}>
                    <Text size={24} bold={true}>Ваша активность</Text>
                    <SelectWeek typeWeek={typeWeek}/>
                </div>
                <div style={{display:"flex", flexDirection:"row", gap:'32px'}}>
                    <div style={{display:"flex", flexDirection:"column", justifyContent:'space-between'}}>
                        <WorkInfoCard count={dayliStatistics?.timeOfWork ?? null} nameDay={'Четверг'}/>
                        <PomidorInfo count={dayliStatistics?.numberPomidors ?? null}/>
                    </div>
                    <BarChart/>
                </div>
                <AdditionalInfoContainer/>
            </div>
        </>
  );
}
